import Showdown from 'showdown'; // search param key name to trigger report generation, value is a report ID

export const GENERATE_REPORT_PARAM = 'visualReportId';
export const GENERATE_REPORT_PARAM_REGEX = new RegExp('[&?]' + GENERATE_REPORT_PARAM + '=[^&]+', '');
export let VISUAL_REPORT_TYPE;

(function (VISUAL_REPORT_TYPE) {
  VISUAL_REPORT_TYPE["dashboard"] = "Dashboard";
  VISUAL_REPORT_TYPE["visualization"] = "Visualization";
  VISUAL_REPORT_TYPE["notebook"] = "Notebook";
})(VISUAL_REPORT_TYPE || (VISUAL_REPORT_TYPE = {}));

export let SELECTOR;

(function (SELECTOR) {
  SELECTOR["dashboard"] = "#dashboardViewport";
  SELECTOR["visualization"] = ".visEditor__content";
  SELECTOR["notebook"] = ".euiPageBody";
})(SELECTOR || (SELECTOR = {}));

export const DEFAULT_REPORT_HEADER = '<h1>Open Distro Kibana Reports</h1>';
export const converter = new Showdown.Converter({
  tables: true,
  simplifiedAutoLink: true,
  strikethrough: true,
  tasklists: true,
  noHeaderId: true
});